.container {
    max-width: 1180px;
	margin: 0 20px 50px;
	box-sizing: border-box;
	overflow: hidden;

	display: grid;
	column-gap: 25px;
	row-gap: 25px;
	font-family: 'Josefin Sans', sans-serif;
}

/* Depth: 2 */

.userTable {
	width: 100%;
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	justify-content: center;
	align-items: center;
}
@media only screen and (max-width: 1200px) { .userTable { grid-template-columns: repeat(3, 1fr) }}
@media only screen and (max-width: 900px) { .userTable { grid-template-columns: repeat(2, 1fr) }}
@media only screen and (max-width: 600px) { .userTable { grid-template-columns: repeat(1, 1fr) }}

.compact {
	width: 50%;
	display: grid;
	row-gap: 5px;
}

.preview {
	position: fixed;
	top: 18vh;
	left: 55%;
	z-index: 2;
	transition: top 0.05s ease;
}
@keyframes scroll-up {
	0% {
		top: 18vh
	}
	80% {
		top: 7vh
	}
	100% {
		top: unset;
		bottom: 1vh;
	}
}

/* Depth 3 */

.title {
	margin: 10px 0;
	font-size: xx-large;
}
.title, .title .menu {
	display: grid;
	grid-template-columns: auto 1fr auto;
	grid-auto-flow: column;
	column-gap: 20px;
	align-items: center;
}
.active {
	opacity: 0.3;
	cursor: pointer;
}
.inactive {
	color: #43853d;
}

.menu label {
	font-size: small;
}
:global(.light) {
	font-weight: 300;
}