.footer {
	display: grid;
	grid-template-columns: auto 1fr;
	grid-template-rows: auto auto;
	width: max-content;
	height: max-content;
	align-self: center;
	justify-self: center;
	margin-top: 20px;
	margin-bottom: 50px;
}

.logoWrapper {
	width: max-content;
}
.logo {
	width: 100px;
	cursor: pointer;
}
.text {
	padding-top: 70px;
	box-sizing: border-box;
	cursor: pointer;
}

.copyright {
	grid-column: span 2;
}